<template>
    <div class="container">
        <div class="main__info">
            <div class="main__left">
                <h2>{{ getPages.title }}</h2>
                <p>{{ getPages.text }}</p>
            </div>
            <div class="main__right">
                <img :src="getPages.img" alt="">
            </div>
        </div>
    </div>
</template>


<script>
import data from '@/data' 
export default {
    data() {
        return {
            pages: data,
            currentId: null
        }
    },
    created() {
        this.currentId = this.$route.params.id
    },
    computed: {
        getPages() {
            return this.pages.find((page) => page.id == this.currentId)
        }
    }
}
</script>